<template>
  <div id="registerThird">

    <!---->

    <div class="container">

      <div class="head2">
        <a href=""><img style="height:45px;width:auto;"></a>
        <h1></h1>
        <p class="login_tip">
          
          <router-link to="/login"></router-link>
        </p>
      </div>
    </div>
    <!---->
    <div class="register_con">
      <div class="register_con_steps">
        <div class="register_con_step">
          <i>1</i>
          <span></span>
        </div>
        <div class="register_con_step ">
          <i>2</i>
          <span></span>
        </div>
        <div class="register_con_step active">
          <i>3</i>
          <span></span>
        </div>
      </div>
      <form>
        <div class="register_con_form">
          <input id="registerId" name="" type="hidden">
          <div class="reg_form_item">
            <label class="for_text"></label>
            <div class="item">
              <input class="text long passwordconf" name="sss" v-model="password" placeholder="" maxlength="20" type="password">
              <i class="correct" style="display: none"></i>
              <p class="tips pass" style="display: none">6-20</p>
            </div>
          </div>
          <div class="reg_form_item">
            <label class="for_text"></label>
            <div class="item">
              <input class="text long repasswordconf" name="sss" placeholder="" maxlength="20" type="password">
              <i class="correct" style="display: none"></i>  
              <p class="tips pass" style="display: none">6-20</p> 
            </div>
          </div>
          <div class="reg_form_item">
            <label class="for_text"></label>
            <div class="item">
              <div class="sub_btn next" @click="submit"></div>
            </div>
          </div>
        </div>
      </form>
    </div>
    <!----> 
    <div class="container">
      <p class="login_footer"> Copyright  2018-2038  ICP17067244</p>
    </div> 
  </div>
</template>

<style>
  
</style>

<script>
  import http from '../../Api/baseHttp'
  import URLString from '../../Api/api'
  
  export default {
  
    data() {
      return {
        password: ""  
      }
    },

    methods: {
      //  
      submit: function() {
  
        var _that = this;
        let params = {
  
          "password": this.password, //
  
          "smsCode": this.$route.params.vertifyCode, //

          "mobile": this.$route.params.phoneNum, //
    
          "name": this.$route.params.phoneNum,
          "userType": "member",

        };
        //
        console.log(params);
        http.post(URLString.registerUser, params, function resCallBack(data) {
          console.log(data);
          if (data.statusCode === 200) {
  
            //
            _that.$toast.center(",3");
  
            let token = data.token;
  
            if (!window.localStorage) {
  
              window.sessionStorage.setItem('userToken', token);
  
            } else {
              window.localStorage.setItem('userToken', token);
            }
            setTimeout(() => {
              that.$router.push({
                name: "PageHome",
                params: {
                  "loginStatus": "1"
                }
              })
            }, 300);  
          } else {
            if (_that.isEmpty(data.message)) {  
              _that.$toast.center("");
            } else {
              _that.$toast.center(data.message);
            }
          }
        });
      },
      isEmpty: function(obj) {
        if (typeof obj == "undefined" || obj == null || obj == "") {
          return true;
        } else {
          return false;
        } 
      }
    }
  }
</script>



