<template>
  <div id="registerSecond">
    <!---->
    <div class="container">
      <div class="head2">
        <a href=""><img style="height:45px;width:auto;"></a>
        <h1></h1>
        <p class="login_tip">
          
          <router-link to="/login"></router-link>
        </p>
      </div>
    </div>
    <!---->
    <div class="register_con">
      <div class="register_con_steps">
        <div class="register_con_step ">
          <i>1</i>
          <span></span>
        </div>
        <div class="register_con_step active">
          <i>2</i>
          <span></span>
        </div>
        <div class="register_con_step">
          <i>3</i>
          <span></span>
        </div>
      </div>
      <div class="register_con_form">
        <input id="registerId" name="" type="hidden">
        <div class="reg_form_item">
          <label class="for_text"></label>
          <div class="item">
            <b>{{this.$route.params.phoneNum}}</b>
          </div>
        </div>
        <div class="reg_form_item">
          <label class="for_text"></label>
          <div class="item">
            <input id="checkCode" class="text medium" placeholder="" name="checkCode" maxlength="4" type="text" v-model="userInputCode">
            <!---->
            <div class="pic_code">
              <identify :identifyCode="identifyCode" style="margin-top:10px;"></identify>
              <span class="code" @click="refreshCode"></span>
            </div>
            <p class="tips mobile_info" style="display: none"></p>
          </div>
        </div>
        <div class="reg_form_item">
          <label class="for_text"></label>
          <div class="item">
            <form>
              <input id="message" v-model="userInputVertifyCode" class="text medium" placeholder="" name="checkCode" maxlength="6" type="text">
            </form>
            <div class="tCode">
              <button id="sendBtn" type="button" class="active_btn btn" @click="getMsgCode">
                    <span id="auto_code"></span>
                  </button>
            </div>
            <p class="tips mobile_info" style="display: none"></p>
          </div>
        </div>
        <div class="reg_form_item">
          <label class="for_text"></label>
          <div class="item">
            <button class="sub_btn free" @click="turnNext"></button>
          </div>
        </div>
      </div>
    </div>
    <!---->
    <div class="container">
      <p class="login_footer"> Copyright  2018-2038  ICPxxxxxx</p>
    </div>
  </div>
</template>

<style>
  @import url(../../assets/css/register);
</style>

<script>
  import SIdentify from '@/components/pictureValidation/identify'
  import http from '../../Api/baseHttp'
  import URLString from '../../Api/api'
  export default {
    data() {
      return {
        identifyCodes: "1234567890",
        identifyCode: "", //
        PhoneNum: "",
        userInputCode: "", //
        vertifyCode: 123, //
        userInputVertifyCode: "" //
      }
    },
    components: {
      identify: SIdentify,
    },
    mounted() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
      this.phoneNum = this.$route.params.phoneNum;
      this.vertifyCode = 123;
    },
    methods: {
      randomNum(min, max) {
        return Math.floor(Math.random() * (max - min) + min);
      },
      refreshCode: function() {
        this.identifyCode = "";
        this.makeCode(this.identifyCodes, 4);
      },
      makeCode(o, l) {
        for (let i = 0; i < l; i++) {
          this.identifyCode += this.identifyCodes[
            this.randomNum(0, this.identifyCodes.length)
          ];
        }
        console.log(this.identifyCode);
      },
      //
      getMsgCode: function() {
        if (this.userInputCode === this.identifyCode) {
          //  -
  
          let reParam = {
            "mobile": this.phoneNum
          };
          var _this = this;
          http.post(URLString.registerCode, reParam, function resCallBack(data) {
            //
            if (data.statusCode == 200) {
              _this.vertifyCode = data.data;
              console.log(data.data);
            } else {
              _this.$toast.center(data.message);
            }
          });
        } else {
          this.$toast.center('');
        }
      },
      //function
      turnNext: function() {
        //1  
        // 2 
        if (this.userInputVertifyCode == this.vertifyCode) {
          this.$router.push({
            name: "RegisterThird",
            params: {
              phoneNum: this.$route.params.phoneNum,
              vertifyCode: this.vertifyCode
            }
          });
        } else {
          this.$toast.center('');
        }
      }
    }
  }
</script>



