      PageFooter
    },
    created (){

    

    },
    mounted() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
      this.initWithWeb();
      this.agreepro();
    },
    methods: {
      randomNum(min, max) {
        return Math.floor(Math.random() * (max - min) + min);
      },
      refreshCode: function() {
        this.identifyCode = "";
        this.makeCode(this.identifyCodes, 4);
      },

      initWithWeb () {
      this.win();
      $(window).resize(function(){
         this.win();
        });
      },

      makeCode(o, l) {
        for (let i = 0; i < l; i++) {
          this.identifyCode += this.identifyCodes[
            this.randomNum(0, this.identifyCodes.length)
          ];
        }
        console.log(this.identifyCode);
      },

      turnNext: function() {
        //
        let re = /^1\d{10}$/
        if (re.test(this.userPhone)) {
          //
  
          if (this.userInputCode == this.identifyCode) {
            this.$router.push({
              name: "RegisterSecond",
              params: {
                phoneNum: this.userPhone
              }
            });
          } else {
            this.$toast.center('');
          }
        } else {
          //
          this.$toast.center('');
        }
      },

      showpro(){
        this.dia(1);
      },

      dia(n){
      $(".mask").fadeIn();
      $(".dia1").fadeIn();
      },

      cls(){
      $(".dialog").fadeOut();
      $(".mask").fadeOut();
      },

      win(){
      var _wd = $(window).width();
      var _hd = $(window).height();
      $(".dialog").css("top",(_hd - $(".dialog").height())/2).css("left",(_wd - $(".dialog").width())/2);
    },

    agreepro(){
      $(".icheckbox_square-green").addClass("checked");
      this.cls();
    }


    }
  }
</script>





