<style>
@import "../../assets/css/personalCenter.css";
</style>

<script>
    import personIntroCenter from '@/components/personal/personintroCenter';
    export default {
        data() {
            return {
                showAtIndex:0,
            }
        },
        components: {
          personIntroCenter
        },
        methods: {
          showWithIndexFunc(val){
              this.showAtIndex = val;
          },
          putDownOrUp(str){
            var obj = $("#"+str);
            var ulObj = $('#'+str+"+.item_list");
            let isHave = obj.hasClass("down");
            console.log(isHave);
              if (isHave) {
                obj.removeClass("down");
                obj.addClass("up");
                ulObj.slideUp("fast");
              } else {
                obj.removeClass("up");
                ulObj.slideDown("fast");
                obj.addClass("down");
              }
          }
        }
    }
</script>



