<template>
    <div class="container">
        <!--  -->
        <div class="location">
            <a href="javascript:;"><strong></strong></a>
            <span>&gt;</span><a href="javascript:;"></a>
            <span>&gt;</span><span>{{this.helpTitle}}</span>
        </div>
    
        <div class="help_cont fr">
            <div class="title">
                <h2>{{this.helpTitle}}</h2>
            </div>
    
            <wuliu-help  :wuliuType ="this.wuliuIndex"> </wuliu-help>
    
        </div>
        <!-- /help_cont -->
        <div class="help_menu fl">
            <div class="help_menu_item">
                <a class="top_link" href="javascript:void(0)"></a>
                <ul>
                    <li :class="{cur:this.leftItemIndex == 1}"><a href="javascript:void(0)" @click="chooseIndex(1)"></a></li>
                    <li :class="{cur:this.leftItemIndex == 2}"><a href="javascript:void(0)" @click="chooseIndex(2)"></a></li>
                    <li :class="{cur:this.leftItemIndex == 3}"><a href="javascript:void(0)" @click="chooseIndex(3)"></a></li>
                    <li :class="{cur:this.leftItemIndex == 4}"><a href="javascript:void(0)" @click="chooseIndex(4)"></a></li>
                </ul>
            </div>
            <div class="help_menu_item">
                <a class="top_link" href="javascript:void(0)"></a>
                <ul>
                    <li :class="{cur:this.leftItemIndex == 6}"><a href="javascript:void(0)" @click="chooseIndex(6)"></a></li>
                </ul>
            </div>
            <div class="help_menu_item">
                <a class="top_link" href="javascript:void(0)"></a>
                <ul>
                    <li :class="{cur:this.leftItemIndex == 8}"><a href="javascript:void(0)" @click="chooseIndex(8)"></a></li>
                    <li :class="{cur:this.leftItemIndex == 10}"><a href="javascript:void(0)" @click="chooseIndex(10)"></a></li>
                    <li :class="{cur:this.leftItemIndex == 11}"><a href="javascript:void(0)" @click="chooseIndex(11)"></a></li>
                </ul>
            </div>
            <div class="help_menu_item">
                <a class="top_link" href="javascript:void(0)" ></a>
                <ul>
                    <li :class="{cur:this.leftItemIndex == 13}"><a href="javascript:void(0)" @click="chooseIndex(13)"></a></li>
                </ul>
            </div>
            <div class="help_menu_item">
                <a class="top_link" href="javascript:void(0)"></a>
                <ul>
                    <li :class="{cur:this.leftItemIndex == 15}"><a href="javascript:void(0)" @click="chooseIndex(15)"></a></li>
                    <li :class="{cur:this.leftItemIndex == 16}"><a href="javascript:void(0)" @click="chooseIndex(16)"></a></li>
                    <li :class="{cur:this.leftItemIndex == 17}"><a href="javascript:void(0)" @click="chooseIndex(17)"></a></li>
                    <li :class="{cur:this.leftItemIndex == 18}"><a href="javascript:void(0)" @click="chooseIndex(18)"></a></li>
                </ul>
            </div>
            <div class="help_menu_item">
                <a class="top_link" href="javascript:void(0)"></a>
                <ul>
                    <li :class="{cur:this.leftItemIndex == 20}"><a href="javascript:void(0)" @click="chooseIndex(20)"></a></li>
                    <li :class="{cur:this.leftItemIndex == 21}"><a href="javascript:void(0)" @click="chooseIndex(21)"></a></li>
                    <li :class="{cur:this.leftItemIndex == 22}"><a href="javascript:void(0)" @click="chooseIndex(22)"></a></li>
                </ul>
            </div>
    
        </div>
        <!-- /help_menu -->
        <div class="cb"></div>
    
        <!-- footer -->
        <page-footer></page-footer>
    </div>
</template>

<style>
    @import "../../assets/css/help.css";
</style>

<script>
    import PageFooter from '@/components/footer/pageFooter'
    import WuliuHelp from '@/components/help/wuliuHelp'

    import http from '../../Api/baseHttp'
    import URLString from '../../Api/api'

    export default {
        name: 'help',
        data() {
            return {
                helpTitle: "",
                leftItemIndex: 1,
                wuliuIndex:1,
            };
        },
        created() {
            this.helpTitle = this.$route.params.title; //  
            this.wuliuIndex = this.$route.params.index; //  
            this.leftItemIndex = this.$route.params.index;
        },
        components: {
            PageFooter,
            WuliuHelp,//body
    
        },
        methods: {
            // body
            chooseIndex(val) {
                this.leftItemIndex = val;
                this.wuliuIndex = val;
                
                if (val == 1) {
                    this.helpTitle = "";
                } else if (val == 2) {
                    this.helpTitle = "";
                } else if (val == 3) {
                    this.helpTitle = "";
                }else if (val == 4) {
                    this.helpTitle = "";
                }else if (val == 8) {
                    this.helpTitle = "";
                }else if (val == 10) {
                    this.helpTitle = "";
                }else if(val == 11){
                    this.helpTitle = "";
                }else if(val == 13){
                    this.helpTitle = "";
                }else if(val == 15){
                    this.helpTitle = "";
                }else if(val == 16){
                    this.helpTitle = "";
                }else if(val == 17){
                    this.helpTitle = "";
                }else if(val == 18){
                    this.helpTitle = "";
                }else if(val == 20){
                    this.helpTitle = "";
                }else if(val == 21){
                    this.helpTitle = "";
                }else {
                    this.helpTitle = "";
                }

            }
        }
    
    }
</script>



